@import "./styles/colors";

.main {
  font-family: "Proxima Nova", Helvetica, Arial, sans-serif;
}

.row {
  display: flex;
  flex-direction: row;
}
header {
  background-color: $primary;
  .container {
    height: 50px;
    width: 80%;
    margin: auto;
    img {
      height: 100%;
    }
    form {
      padding-left: 30px;
      flex: 1;
      display: flex;
      flex-direction: row;
      align-items: center;
      input[type="text"] {
        border: 0 rgba(0, 0, 0, 0.2);
        color: #333;
        font-size: 15px;
        width: 100%;
        padding: 10px;
        font-family: inherit;
      }
      button {
        background-color: #ebebeb;
        height: 37px;
        padding: 8px;
        cursor: pointer;
        border: none;
        i {
          color: #666;
        }
      }
    }
  }
}

.main {
  margin: auto;
  width: 90%;
  background-color: #eeeeee;
  .results {
    background-color: white;
    flex: 1;
    padding-top: 12px;
  }
}

.product {
  .image {
    flex: 1;
  }
  .detail {
    flex: 2;
    .price {
      font-weight: lighter;
    }
  }
  .location {
    flex: 1;
  }
}
